import React from "react";
import { Splide, SplideSlide, SplideTrack } from "@splidejs/react-splide";
import { StaticImage } from "gatsby-plugin-image";

export default function YorudanDistrikt() {
  const [videoOpen, setVideoOpen] = React.useState(false);
  const videoRef = React.useRef(null);

  function handleVideoOpen() {
    setVideoOpen(true);
    videoRef.current.play();
  }

  function handleVideoClose() {
    setVideoOpen(false);
    videoRef.current.pause();
  }

  return (
    <div className="scroll-snap" id="basket">
      <div className="grid">
        <div className="grid-item img">
          <StaticImage
            src="../images/basketball.png"
            alt="Basket"
            width={500}
            height={500}
            placeholder="blurred"
          />
        </div>
        <div className="grid-item content">
          <div className="title">
            <h1>Yorudan Distrikt</h1>
            <button onClick={handleVideoOpen}>Play Video</button>
          </div>
          <p>
            In a place where basketball is the supreme spectacle, three
            teenagers aged between 17 and 19 will make their way through a local
            underground championship getting the attention of everyone. With its
            own streaming service, the Street League is growing in popularity.
            Threatening the rating profits of its counterpart The Pro League,
            run by powerful Giliad Corp, which will do everything in its power
            to shut them down.
          </p>
        </div>
      </div>
      <div className="slide-container">
        <Splide
          options={{
            perPage: 1,
            perMove: 1,
            gap: "1rem",
            height: 600,
            width: "100%",
            pagination: false,
            wheel: false,
            arrows: true,
            type: "loop",
            breakpoints: {
              1300: {
                height: 500,
              },
              1024: {
                height: 400,
              },
              540: {
                height: 360,
              },
              400: {
                height: 300,
              },
            },
          }}
          className="slider"
          hasTrack={false}
        >
          <SplideTrack>
            {characters.map((character, idx) => (
              <SplideSlide key={idx}>
                <div className="slide">
                  <img src={character.image} alt={character.name} />
                  <p className="caption">{character.name}</p>
                </div>
              </SplideSlide>
            ))}
          </SplideTrack>
          <div className="splide__arrows"></div>
        </Splide>
      </div>
      <div className={`modal ${videoOpen ? "opened" : ""}`}>
        <div
          className="modal-overlay"
          role="button"
          tabIndex={0}
          onClick={handleVideoClose}
          onKeyDown={handleVideoClose}
        >
          {" "}
        </div>
        <div className="modal-content">
          <button className="close" onClick={handleVideoClose}>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
              <path d="M0 0h24v24H0z" fill="none" />
              <path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z" />
            </svg>
          </button>
          <video controls autoPlay={videoOpen} ref={videoRef}>
            <source
              src="https://sxakil.github.io/distrikt/basket.mp4"
              type="video/mp4"
            />
            <track kind="captions" />
          </video>
        </div>
      </div>
    </div>
  );
}
