import SweetScroll from "sweet-scroll";
import React, { useEffect, useRef } from "react";

import wheel from "../images/wheel.svg";

const imgs = {
  basket: {
    png: "https://sxakil.github.io/distrikt/imgs/basketball.png",
    gif: "https://sxakil.github.io/distrikt/imgs/basketball.gif",
    link: "#basket",
  },
  football: {
    png: "https://sxakil.github.io/distrikt/imgs/football.png",
    gif: "https://sxakil.github.io/distrikt/imgs/football.gif",
    link: "#football",
  },
  graffiti: {
    png: "https://sxakil.github.io/distrikt/imgs/graffiti.png",
    gif: "https://sxakil.github.io/distrikt/imgs/graffiti.gif",
    link: "#graffiti",
  },
  quad: {
    png: "https://sxakil.github.io/distrikt/imgs/quad.png",
    gif: "https://sxakil.github.io/distrikt/imgs/quad.gif",
    link: "#skate",
  },
  skate: {
    png: "https://sxakil.github.io/distrikt/imgs/skate.png",
    gif: "https://sxakil.github.io/distrikt/imgs/skate.gif",
    link: "#skate",
  },
};

export default function World() {
  const world = useRef(null);
  const sweetScroll = useRef(null);

  useEffect(() => {
    sweetScroll.current = new SweetScroll({
      easing: "easeOutCubic",
    });
  }, []);

  function handleClick(goTo) {
    sweetScroll.current.to(goTo);
  }

  function handleMouseEnter(e, key) {
    e.target.src = imgs[key].gif;
    world.current.classList.add(key);
  }
  function handleMouseLeave(e, key) {
    e.target.src = imgs[key].png;
    world.current.classList.remove(key);
  }
  return (
    <div className="scroll-snap world" id="world" ref={world}>
      <div className="world-contents">
        {Object.keys(imgs).map((key) => (
          <div
            role="button"
            tabIndex={0}
            key={key}
            to={imgs[key].link}
            onClick={() => handleClick(imgs[key].link)}
            onKeyDown={() => handleClick(imgs[key].link)}
            onMouseEnter={(e) => handleMouseEnter(e, key)}
            onMouseLeave={(e) => handleMouseLeave(e, key)}
          >
            <img className={key + "-img"} src={imgs[key].png} alt={key} />
          </div>
        ))}
        <img className="wheel" src={wheel} alt="wheel" />
      </div>
    </div>
  );
}
