import React, { useEffect, useState } from "react";

import Navbar from "./Navbar";

const Layout = ({ children }) => {
  const [backgroundSize, setBackgroundSize] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      const W = window.innerWidth;
      const H = window.innerHeight;
      if (W <= 969) {
        setBackgroundSize("cover");
        return;
      }
      const zoom = window.pageYOffset / H;
      if (zoom <= 3) {
        let newHeight = H + H * zoom;
        let newWidth = newHeight * 1.9;
        if (newWidth < W) {
          newWidth = W;
          newHeight = W / 1.9;
        }
        setBackgroundSize(`${newWidth}px ${newHeight}px`);
      }
    };
    window.addEventListener("scroll", handleScroll);
    window.addEventListener("resize", handleScroll);
    handleScroll();
    return () => {
      window.removeEventListener("scroll", handleScroll);
      window.removeEventListener("resize", handleScroll);
    };
  }, []);

  return (
    <>
      <div className="bg" style={{ backgroundSize }}>
        <div className="mask" />
      </div>
      <div className="layout">
        <Navbar />
        {children}
      </div>
    </>
  );
};

export default Layout;
