import React from "react";
import { Splide, SplideSlide, SplideTrack } from "@splidejs/react-splide";
import { StaticImage } from "gatsby-plugin-image";

export default function Graffiti() {
  return (
    <div className="scroll-snap" id="graffiti">
      <div className="grid">
        <div className="grid-item img">
          <StaticImage
            src="../images/graffiti.png"
            alt="Graffiti"
            width={500}
            height={500}
            placeholder="blurred"
          />
        </div>
        <div className="grid-item content">
          <h1>Graff City</h1>
          <p>
            No one invites you into Graffiti. Everyone gets there for different
            reasons. For the Neverland crew, its a scream to be heard, since no
            one in Giliad seems to hear the needs of people in the Distrikt.
            YutaBots reinforce the interests of Giliad Corp with violence. But
            there are some stories that can't be silenced.
          </p>
        </div>
      </div>
      <div className="slide-container">
        <Splide
          options={{
            perPage: 1,
            perMove: 1,
            gap: "1rem",
            height: 536,
            width: "100%",
            pagination: false,
            wheel: false,
            arrows: true,
            breakpoints: {
              1300: {
                height: 500,
              },
              1024: {
                height: 400,
              },
              768: {
                height: undefined,
                autoHeight: true,
              },
            },
          }}
          className="slider"
          hasTrack={false}
        >
          <SplideTrack>
            {characters.map((character, idx) => (
              <SplideSlide key={idx}>
                <div className="slide">
                  <img src={character.image} alt={character.name} />
                  <p className="caption">{character.name}</p>
                </div>
              </SplideSlide>
            ))}
          </SplideTrack>
          <div className="splide__arrows"></div>
        </Splide>
      </div>
    </div>
  );
}
const characters = [
  {
    name: "",
    image:
      "https://sxakil.github.io/distrikt/characters/graffiti/graffiti_1.png",
  },
  {
    name: "",
    image:
      "https://sxakil.github.io/distrikt/characters/graffiti/graffiti_2.png",
  },
  {
    name: "",
    image:
      "https://sxakil.github.io/distrikt/characters/graffiti/graffiti_3.png",
  },
];
