import SweetScroll from "sweet-scroll";
import React, { useEffect, useRef, useState } from "react";
import { StaticImage } from "gatsby-plugin-image";

export default function Navbar() {
  const [scrollTriggered, setScrollTriggered] = useState(false);
  const [menuOpen, setMenuOpen] = useState(false);
  const [nestedMenuOpen, setNestedMenuOpen] = useState(false);
  const sweetScroll = useRef(null);
  useEffect(() => {
    const handleScroll = () => {
      if (window.pageYOffset > 50) {
        setScrollTriggered(true);
      } else {
        setScrollTriggered(false);
      }
    };
    window.addEventListener("scroll", handleScroll);
    sweetScroll.current = new SweetScroll({
      easing: "easeOutCubic",
    });
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);
  function handleClick(goTo) {
    sweetScroll.current.to(goTo);
    setMenuOpen(false);
  }
  function toggleMenu() {
    setMenuOpen(!menuOpen);
  }
  function toggleNestedMenu() {
    setNestedMenuOpen(!nestedMenuOpen);
  }
  return (
    <nav className="nav-bar">
      <div className="nav-bg" style={{ opacity: scrollTriggered ? 1 : 0 }} />
      <div
        className="brand"
        role="button"
        tabIndex={0}
        onClick={() => handleClick("#index")}
        onKeyDown={() => handleClick("#index")}
      >
        <StaticImage
          src="../images/distrikt-brand.png"
          alt="Distrikt"
          width={130}
          placeholder="blurred"
        />
      </div>
      <ul className="pages">
        <li className="n-about">
          <div
            role="button"
            tabIndex={0}
            onClick={() => handleClick("#about")}
            onKeyDown={() => handleClick("#about")}
          >
            About
          </div>
        </li>
        <li className="n-distrikt-pass n-airdrops n-main-collection">
          <div className="dropdown">
            <div className="arrow-down" />
            <span title="Non Fungible Tokens">NFTs</span>
            <div className="dropdown-wrapper">
              <ul>
                <li>
                  <a
                    href="https://www.jpg.store/collection/distriktpass"
                    target="_blank"
                    rel="noreferrer"
                  >
                    Distrikt Pass
                  </a>
                </li>
                <li>
                  <a
                    href="https://www.jpg.store/collection/distriktairdrops?tab=items"
                    target="_blank"
                    rel="noreferrer"
                  >
                    Airdrops
                  </a>
                </li>
                <li>
                  <a href="https://sxakil.github.io/distrikt/#">
                    Main Collection
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </li>
        <li className="n-world">
          <div
            role="button"
            tabIndex={0}
            onClick={() => handleClick("#world")}
            onKeyDown={() => handleClick("#world")}
          >
            World
          </div>
        </li>
        <li className="n-crew">
          <div
            role="button"
            tabIndex={0}
            onClick={() => handleClick("#crew")}
            onKeyDown={() => handleClick("#crew")}
          >
            Crew
          </div>
        </li>
        <li className="n-faq">
          <div
            role="button"
            tabIndex={0}
            onClick={() => handleClick("#faq")}
            onKeyDown={() => handleClick("#faq")}
            title="Frequently Asked Questions"
          >
            FAQ
          </div>
        </li>
      </ul>
      <ul className="socials">
        <li>
          <a href="https://www.instagram.com/sobradojuanma" title="Instagram">
            <StaticImage
              src="../images/instagram.png"
              alt="Instagram"
              width={20}
              placeholder="blurred"
            />
          </a>
        </li>
        <li>
          <a href="https://twitter.com/DISTRIKTCNFT" title="Twitter">
            <StaticImage
              src="../images/twitter.png"
              alt="Twitter"
              width={20}
              placeholder="blurred"
            />
          </a>
        </li>
        <li>
          <a href="https://discord.gg/EApn2qm8" title="Discord">
            <StaticImage
              src="../images/discord.png"
              alt="Discord"
              width={20}
              placeholder="blurred"
            />
          </a>
        </li>
      </ul>
      {menuOpen && (
        <div
          className="overlay"
          role="button"
          tabIndex={0}
          aria-label="Menu"
          onClick={toggleMenu}
          onKeyDown={toggleMenu}
        />
      )}
      <div id="menuToggle">
        <div
          className={`ham ${menuOpen ? "open" : ""}`}
          role="button"
          tabIndex={0}
          onClick={toggleMenu}
          onKeyDown={toggleMenu}
        >
          <span />
          <span />
          <span />
        </div>
        <ul id="mobileMenu" className={menuOpen ? "open" : ""}>
          <li>
            <div
              role="button"
              tabIndex={0}
              onClick={() => handleClick("#about")}
              onKeyDown={() => handleClick("#about")}
            >
              About
            </div>
          </li>
          <li>
            <div
              role="button"
              tabIndex={0}
              onClick={() => handleClick("#world")}
              onKeyDown={() => handleClick("#world")}
            >
              World
            </div>
          </li>
          <li>
            <div
              className="nft-menu"
              role="button"
              tabIndex={0}
              onClick={toggleNestedMenu}
              onKeyDown={toggleNestedMenu}
            >
              <span title="Non Fungible Tokens">NFTs</span>
              <div className={`arrow-down ${nestedMenuOpen ? "open" : ""}`} />
            </div>
          </li>
          <li className={`nested-menu ${nestedMenuOpen ? "open" : ""}`}>
            <ul>
              <li>
                <a
                  href="https://www.jpg.store/collection/distriktpass"
                  target="_blank"
                  rel="noreferrer"
                >
                  Distrikt Pass
                </a>
              </li>
              <li>
                <a
                  href="https://www.jpg.store/collection/distriktairdrops?tab=items"
                  target="_blank"
                  rel="noreferrer"
                >
                  Airdrops
                </a>
              </li>
              <li>
                <a href="https://sxakil.github.io/distrikt/#">
                  Main Collection
                </a>
              </li>
            </ul>
          </li>
          <li>
            <div
              role="button"
              tabIndex={0}
              onClick={() => handleClick("#crew")}
              onKeyDown={() => handleClick("#crew")}
            >
              Crew
            </div>
          </li>
          <li>
            <div
              role="button"
              tabIndex={0}
              onClick={() => handleClick("#faq")}
              onKeyDown={() => handleClick("#faq")}
              title="Frequently Asked Questions"
            >
              FAQ
            </div>
          </li>
        </ul>
      </div>
    </nav>
  );
}
